body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.empty-input{
  font-size: 14px;
  color: #ff0505;
  padding: 0;
}
.success-msg{
  color: #0dd452;
  padding: 10px 15px;
}
.error-msg{
  color: #ff0505;
  padding: 10px 15px;
}


/* HOMEPAGE GITHUB LINKEDIN ICONS */
.github-icon{
  transition: 0.2s all ease-in-out;
  color: #313030;
}

.linkedin-icon{
  transition: 0.2s all ease-in-out;
  color: #0077B5;
}

.github-icon:hover{
  color: #929B9A;
}

.linkedin-icon:hover{
  color: #929B9A;
}
/* END HOMEPAGE GITHUB LINKEDIN ICONS */


.m-card{
  margin: 20px;

}

.m-card-image{
  
  border-radius: 10px;
  width: 300px;
  height: 300px;
  box-shadow: 0px 0px 3px 1px #ccc;

}


.m-card-info{
  margin-top: 10px;
  min-height: 100px;

}

.m-card-title{
  font-size: 24px;
  margin: 0px;
}

.m-card-subTitle{
  font-size: 16px;
  margin: 0px;

}

/* CARD ROTATE HOVER EFFECT */
.card-container{
  width: 300px;
  height: 300px;
}

.card{
    width: 100%;
    height:100%;
    transition: transform 1s ease-in-out;
    transform-style: preserve-3d;
}

.card-container:hover .card{

  transform: rotateY(180deg);
}


.card figure{
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0;
}

.card .front{

}


.card .back{
  padding: 0 5%;
  width: 301px;
  height: 301px;
  background: white;
  transform: rotateY(180deg);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}